import React from "react";
import makeStyles from "@mui/styles/makeStyles";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { useNavigate, useParams } from "react-router-dom";
import { connect } from "react-redux";
import doctorDefault from "../../assets/doctors.png";
import { TopNav } from "../../components";
import { Container } from "@mui/material";
import { BASE_URL_IMG } from "../../helpers/common/config";

const useStyles = makeStyles(() => ({
  img: {
    width: "100%",
    // height: "auto",
    height: "500px",
    objectFit: "contain",
    position: "absolute",
    zIndex: "-1",
    top: 0,
    left: 0,
    right: 0,
  },
  footer: {
    position: "absolute",
    bottom: 0,
    left: 0,
    right: 0,
    height: "60%",
    padding: "32px",
    borderRadius: "32px 32px 0 0 !important",
  },
  container: {
    marginTop: "32px",
  },
}));
const BoxSum = ({ title, subtitle }) => {
  return (
    <Box border={1} padding={1} borderRadius={3}>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <Typography variant="body1">{title}</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="subtitle1">{subtitle}</Typography>
        </Grid>
      </Grid>
    </Box>
  );
};
const DetailDoctor = (props) => {
  const classes = useStyles();
  let { doctorId } = useParams();
  const navigate = useNavigate();

  const { stateDetailDokter } = props;
  return (
    <>
      <Container className={classes.container}>
        <TopNav
          type="back"
          title={"Detail Dokter"}
          click={() => {
            navigate(-1);
          }}
        />
      </Container>
      <img
        className={classes.img}
        src={
          stateDetailDokter.filename
            ? BASE_URL_IMG + '/' + stateDetailDokter.filename
            : doctorDefault
        }
        alt=""
      />
      <Paper className={classes.footer}>
        <Grid container spacing={2}>
          <Grid item container spacing={2} xs={12}>
            <Grid item xs={6}>
              <Grid item xs={12}>
                <Typography variant="subtitle1">
                  {stateDetailDokter.nama_dokter}
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography variant="body1">
                  {stateDetailDokter.ref_layanan_nama}
                </Typography>
              </Grid>
            </Grid>
            <Grid item xs={6}>
              <Grid item xs={12}>
                <Typography align="right" variant="body1">
                  Mulai Dari
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography align="right" variant="subtitle1">
                  -
                </Typography>
              </Grid>
            </Grid>
          </Grid>
          <Grid item container spacing={2}>
            <Grid item xs>
              <BoxSum title={"Pasien"} subtitle={"-"} />
            </Grid>
            <Grid item xs>
              <BoxSum title={"Pengalaman"} subtitle={"-"} />
            </Grid>
            <Grid item xs>
              <BoxSum title={"Review"} subtitle={"-"} />
            </Grid>
          </Grid>
          <Grid item container spacing={2}>
            <Grid item xs={12}>
              <Button variant="contained" disabled>
                Buat Jadwal
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Button variant="outlined">Lihat Jadwal Praktek</Button>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateDetailDokter: state.master.detailDoctor || "",
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    // actResetVaksin: () => dispatch(resetVaksin()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(DetailDoctor);
